import { setHasElement } from './set'

export const PHYSICAL_ITEM_TYPES = new Set([
    'armor',
    'backpack',
    'book',
    'consumable',
    'equipment',
    'treasure',
    'weapon',
] as const)

export const dcByLevel = new Map([
    [-1, 13],
    [0, 14],
    [1, 15],
    [2, 16],
    [3, 18],
    [4, 19],
    [5, 20],
    [6, 22],
    [7, 23],
    [8, 24],
    [9, 26],
    [10, 27],
    [11, 28],
    [12, 30],
    [13, 31],
    [14, 32],
    [15, 34],
    [16, 35],
    [17, 36],
    [18, 38],
    [19, 39],
    [20, 40],
    [21, 42],
    [22, 44],
    [23, 46],
    [24, 48],
    [25, 50],
])

export const dcAdjustments = new Map([
    ['incredibly easy', -10],
    ['very easy', -5],
    ['easy', -2],
    ['normal', 0],
    ['hard', 2],
    ['very hard', 5],
    ['incredibly hard', 10],
])

export const MAGIC_TRADITIONS = new Set(['arcane', 'divine', 'occult', 'primal']) as Set<MagicTradition>

export function adjustDC(dc: number, adjustment: DCAdjustment = 'normal') {
    return dc + (dcAdjustments.get(adjustment) ?? 0)
}

export function rarityToDCAdjustment(rarity: Rarity = 'common'): PositiveDCAdjustment {
    if (rarity === 'uncommon') return 'hard'
    else if (rarity === 'rare') return 'very hard'
    else if (rarity === 'unique') return 'incredibly hard'
    else return 'normal'
}

export function adjustDCByRarity(dc: number, rarity: Rarity = 'common') {
    return adjustDC(dc, rarityToDCAdjustment(rarity))
}

export function calculateDC(level: number, { proficiencyWithoutLevel = false, rarity = 'common' }: DCOptions = {}) {
    // assume level 0 if garbage comes in. We cast level to number because the backing data may actually have it
    // stored as a string, which we can't catch at compile time
    const dc = dcByLevel.get(level) ?? 14
    if (proficiencyWithoutLevel) {
        // -1 shouldn't be subtracted since it's just
        // a creature level and not related to PC levels
        return adjustDCByRarity(dc - Math.max(level, 0), rarity)
    } else {
        return adjustDCByRarity(dc, rarity)
    }
}

/** Extract all traits from an item, that match a magic tradition */
function getMagicTraditions(item: PhysicalItemPF2e) {
    const traits = item.system.traits.value
    return new Set(traits.filter(t => setHasElement(MAGIC_TRADITIONS, t)))
}

export function identifyMagic(item: PhysicalItemPF2e, baseDc: number, notMatchingTraditionModifier: number) {
    const result = {
        occult: baseDc,
        primal: baseDc,
        divine: baseDc,
        arcane: baseDc,
    }
    const traditions = getMagicTraditions(item)
    for (const key of MAGIC_TRADITIONS) {
        // once an item has a magic tradition, all skills
        // that don't match the tradition are hard
        if (traditions.size > 0 && !traditions.has(key)) {
            result[key] = baseDc + notMatchingTraditionModifier
        }
    }
    return { arc: result.arcane, nat: result.primal, rel: result.divine, occ: result.occult }
}

/** All cursed items are incredibly hard to identify */
export function getDcRarity(item: PhysicalItemPF2e) {
    return item.traits.has('cursed') ? 'unique' : item.rarity
}

export function identifyItem(
    item: PhysicalItemPF2e,
    { proficiencyWithoutLevel = false, notMatchingTraditionModifier }: IdentifyItemOptions,
    noDC = false
) {
    const baseDC = calculateDC(item.level, { proficiencyWithoutLevel })
    const rarity = getDcRarity(item)
    const dc = adjustDCByRarity(baseDC, rarity)
    if (item.isMagical) return identifyMagic(item, dc, notMatchingTraditionModifier)
    if (!noDC) return { cra: dc }
    if (item.isAlchemical) return { cra: dc }
    return { dc: dc }
}

export function hasInvestedProperty(source: ItemSourcePF2e): source is MagicItemSource {
    return isPhysicalData(source) && 'invested' in source.system.equipped
}

/** Checks if the given item data is a physical item with a quantity and other physical fields. */
export function isPhysicalData(source: ItemSourcePF2e): source is PhysicalItemSource
export function isPhysicalData(source: PreCreate<ItemSourcePF2e>): source is PreCreate<PhysicalItemSource>
export function isPhysicalData(source: ItemSourcePF2e | PreCreate<ItemSourcePF2e>): boolean {
    return setHasElement(PHYSICAL_ITEM_TYPES, source.type)
}
