import MODULE_ID from '~./src/module'

type OwnModule = 'anonymous' | 'idleuh'

type ReturnedModule<T extends Record<string, any> = Record<string, any>> = Module<T> | undefined
type ReturnedModuleApi<T extends Record<string, any> = Record<string, any>> = Module<T>['api'] | undefined

export function getModule(id: 'anonymous'): ReturnedModule<AnonymousApi>
export function getModule(id: 'idleuh'): ReturnedModule<IdleuhApi>
export function getModule(id: 'pf2e-hero-actions'): ReturnedModule<HeroActionsApi>
export function getModule(id: string): ReturnedModule
export function getModule(id: string | OwnModule) {
    return game.modules.get(id)
}

export function getCurrentModule() {
    return getModule(MODULE_ID)!
}

export function getModuleApi(id: 'anonymous'): ReturnedModuleApi<AnonymousApi>
export function getModuleApi(id: 'idleuh'): ReturnedModuleApi<IdleuhApi>
export function getModuleApi(id: 'pf2e-hero-actions'): ReturnedModuleApi<HeroActionsApi>
export function getModuleApi(id: string): ReturnedModuleApi
export function getModuleApi(id: string | OwnModule) {
    return getModule(id)?.api
}
